export default {
	data() {
		return {
			screenHeight: 0, //屏幕高度
			isLoading: false, //防止频繁触发
			bottomDistinct: 0, //距离底部多少像素时触发
			pageObj: {
				page: 0,
				pagecount: 0
			},
			noMore: false,
			noData:false,
			isScroll: false,
			fansList: []
		}
	},
	methods: {
		getAllFans() {
			var that = this;
			this.$apiJs.myFans({
				page: this.pageObj.page + 1
			}).then(res => {
				var r_data = res.backlist;
				this.fansList = r_data;
			})
		}
	},
	mounted() {
		this.getAllFans();
	}
}
